::-webkit-scrollbar { 
    display: none; 
}
#element::-webkit-scrollbar { 
    display: none; 
}

#root, html {
  width: 100%;
  height: 100%;
  margin: 0;
}
body{
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: scroll;
  font-family: sans-serif;
}

#container{
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

#mainPost{
  min-width: calc(100% - 300px);
  width: 80%;
  margin-left: 20%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: flex-start;
}

#mainPost .row{
  width: 100%;
}

#mainPost .postOuterContainer{
  width: 100%;
  height: 100vh;
  position: relative;
  text-align: center;
}

#mainPost .postBackgroundImage{
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  filter: grayscale(60%) blur(10px);
  /*Remove white between boxes*/
  width: 104%;
  height: 104%;
  position: absolute;
}

#mainPost img{
  height: 100%;
  position: relative;
}

#postOverview{
  position: fixed;
  width: 20%;
  min-width: 300px;
  height: 100%;
  background-color: rgba(0,0,0,0.8);
  z-index: 1;
  overflow: scroll;
}

#postOverview .favoriteImageDelete{
  position: absolute;
  width: 20px;
  height: 20px;
  left: calc(100% - 25px);
  padding-top: 5px;
  cursor: pointer;
  z-index: 10;
}

#postOverview .settingsContainer{
  color:white;
  text-align: center;
  overflow: auto;
  margin-bottom: 15px;
}

#postOverview h3{
  margin-bottom: 5px;
}

#postOverview ul{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

#postOverview ul li{
  margin: 0;
  padding: 0;
  color:white;
  width: 100%;
}

#postOverview ul li a{
  color:white;
  text-decoration: none;
}

#postOverview ul li a:hover{
  color:white;
  text-decoration: underline;
}

#postOverview ul li .favoriteImageContainer{
  height: 175px;
  position: relative;
}

#postOverview ul li .imageLink{
  display: block;
  position: relative;
  background-size: cover;
  background-position: center center;
  height: 100%;
  width: 100%;
}

#postOverview ul li .favoriteLink{
  padding-left: 5px;
}

#postOverview ul li .favoriteInfo{
  top: 0;
  position: absolute;
  background-color: rgba(0,0,0,0.7);
  padding: 5px;
}

#postOverview ul li p{
  float: left;
  color: white;
}

#postOverview .searchInputContainer{
  text-align: center;
  margin-top:40px;
  margin-bottom: 15px;
}

#postOverview input{
  border-radius: 20px;
  padding-top: 7px;
  padding-bottom: 7px;
  padding-left: 20px;
  font-size: 16pt;
  text-transform: lowercase;
}

#postOverview .error{
  color: red;
  text-align: center;
  font-weight: bold;
}

